 * {
     margin: 0;
     padding: 0;
     font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
 }

 * a {
     cursor: pointer;
 }

 .noDecoratioin {
     color: black;
     text-decoration: none;
 }

 #root {
     height: 100%;
     width: 100%;
 }

 html,
 body {
     height: 100vh;
     width: 100vw;
     background-color: rgb(245 245 245);
     overflow-x: hidden;
 }

 .mainBody {
     position: relative;
     display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: space-between;
     margin-top: 3.5rem;
     background-color: var(--body);
     height: 100%;
 }

 .main {
     display: flex;
     flex-direction: column;
     height: 100%;
     width: 100%;
     background-color: rebeccapurple;
 }

 .mainBody .content {
     width: 80rem;
 }

 footer {
     background-color: rgb(229, 229, 229);
     height: 20vh;
     width: 100vw;
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: center;
     padding: 1rem;
     font-size: 0.9rem;
     color: rgb(64, 64, 64);
 }